.container1 {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.container1 h1 {
  margin-bottom: 15vh;
}
.container2 {
  width: 50%;
  height: 100%;
  background-size: 50vw 100vh;
  background-image: url("../../images/MeetingPage.png");
  background-repeat: no-repeat;
}
.buttons button {
  margin: 2vw;
  height: 6vh;
  width: 10vw;
  color: white;
  font-weight: 600;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  background: transparent
    linear-gradient(270deg, #a2d2ff 0%, #cdb4db 100%, #516980 100%) 0% 0%
    no-repeat padding-box;
}
.buttons button:hover {
  box-shadow: black 1px 1px 6px;
}
@media screen and (max-width: 480px) {
  .container2 {
    display: none;
  }
  .container1 {
    width: 100%;
  }
  .buttons button {
    height: 7vh;
    width: 20vw;
    margin: 4vw;
  }
}
